import { Injectable } from "@angular/core";
import { Price, PriceCalculator } from "./index";
import { PriceCalculatorServiceBase } from "./abstraction/priceCalculatorServiceBase";
const _ = require("lodash");

@Injectable()
export class PriceCalculatorByHours extends PriceCalculatorServiceBase implements PriceCalculator {
    private hours: number;
    private timesYear: number;

    constructor() {
        super();
    }

    public setHours(value: number) {
        this.hours = _.toNumber(value);
    }

    public setTimesYear(value: number) {
        this.timesYear = _.toNumber(value);
    }

    public calculate(): Price {
        // beräkningar börjar här
        const c = this.pris;
        const cost1 = c.utanReduction;
        const cost2 = c.medReduction;
        const costMat = c.material;

        // beräkningar per månad = timme * pris * tillfällen per år.
        cost1.exMoms = (this.hours * c.styckPris.exMoms * this.timesYear) / 12;

        // beräkna material kostnaden
        const materialMoms = parseInt("0.0", 10) + this.materialMoms;
        costMat.exMoms = cost1.exMoms * materialMoms;

        // beräkna skattereduktionen genom kostnaden av cost1 ex moms.
        cost2.exMoms = cost1.exMoms * this.skattReduction;

        // lägg till övriga kostnader
        let travelCost = (this.timesYear * this.resekostnad) / 12;
        cost1.exMoms += costMat.exMoms + travelCost;
        cost2.exMoms += costMat.exMoms + travelCost;

        // beräkna kostnaden inc moms.
        costMat.incMoms = costMat.exMoms * this.moms;
        cost1.incMoms = cost1.exMoms * this.moms;
        cost2.incMoms = cost2.exMoms * this.moms;

        // avrunda uppåt till närmaste heltal.
        cost1.exMoms = this.roundUp(cost1.exMoms);
        cost2.exMoms = this.roundUp(cost2.exMoms);

        cost1.incMoms = this.roundUp(cost1.incMoms);
        cost2.incMoms = this.roundUp(cost2.incMoms);

        return c;
    }
}



