import { Price, PriceSet } from "../index";
const _ = require("lodash");

export abstract class PriceCalculatorServiceBase {
    pris: Price;
    resekostnad: number;
    materialMoms: number;
    skattReduction: number;
    moms: number;
    isIncMoms: boolean;
    protected inizialized: boolean = false;

    constructor() {
        this.pris = new Price();
    }

    public setPrice(set: PriceSet) {
        if (this.inizialized === false) {
            this.inizialized = true;

            // Ensure that we calculate against 1.
            set.vatRate = parseInt("1.0", 10) + set.vatRate;
        }

        set.unitPrice = _.toNumber(set.unitPrice);

        let a = this.pris.styckPris;
        this.resekostnad = set.travelCost;
        this.materialMoms = set.materialVat;
        this.skattReduction = set.discountRate;
        this.moms = set.vatRate;
        this.isIncMoms = set.incVat;

        if (this.skattReduction === 0) {
            this.skattReduction = 1;
        }

        if (set.incVat) {
            a.exMoms = set.unitPrice / set.vatRate;
            a.incMoms = set.unitPrice;
        } else {
            a.exMoms = set.unitPrice;
            a.incMoms = set.unitPrice * set.vatRate;
        }
    }

    protected roundUp(p: number): number {
        // avrunda upp till närmaste krona.
        return (Math.ceil(p / 1)) * 1;
    }
}



