export class PriceSet {
    public name: string;
    public unitPrice: number;
    public discountRate: number = 0.50;
    public incVat: boolean = false;
    public vatRate: number = 0.25;
    public materialVat: number = 0.04;
    public travelCost: number = 0;

    constructor(name: string, unitPrice: number, discountRate: number, incVat: boolean, vatRate: number, materialVat: number, travelCost: number) {
        this.name = name;
        this.unitPrice = unitPrice;
        this.discountRate = discountRate;
        this.incVat = incVat;
        this.vatRate = vatRate;
        this.materialVat = materialVat;
        this.travelCost = travelCost;
    }
}



