import { Component, NgZone, Inject, Input, ElementRef, OnInit } from "@angular/core";
import { FormBuilder } from "@angular/forms";
import { DomSanitizer } from "@angular/platform-browser";
import * as $ from "jquery";
import * as bl from "../../../blueleet";
import { ValidationService } from "../../../validation.service";
import * as index from "../index";
import * as PriceSet from "../entities/priceSet";
import { WidgetPriceCalculatorComponent, IWidgetPriceCalculatorOptions } from "./widget-priceCalculator.component";
const _ = require("lodash");

export class PriceVariation {
    public heading: string;
    public unitPrice: number;
}

export interface IWidgetPriceCalculatorBySquareMetersOptions extends IWidgetPriceCalculatorOptions {
    priceVariations: PriceVariation[];
}

@Component({
    selector: "widget-price-calculator-by-square-meters",
    template: require("./templates/widget-priceCalculatorBySquareMeters.html"),
    styles: [require("./widget-priceCalculator.scss").toString()],
    providers: [index.PriceCalculatorBySquareMeters]
})
export class WidgetPriceCalculatorBySquareMetersComponent extends WidgetPriceCalculatorComponent<IWidgetPriceCalculatorBySquareMetersOptions> implements OnInit, IWidgetPriceCalculatorBySquareMetersOptions {
    activeVariationIndex: number = 0;

    /*
    *  Editor settings
    */
    @Input() variationsTitle: string;

    /*
    *  Calculator settings
    */
    @Input() unitPrice: number;
    @Input() discountRate: number;
    @Input() incVat: boolean;
    @Input() vatRate: number;
    @Input() materialVat: number;
    @Input() travelCost: number;
    @Input() priceVariations: PriceVariation[] = [];

    constructor(
        @Inject(NgZone) zone: NgZone,
        @Inject(FormBuilder) formBuilder: FormBuilder,
        @Inject(DomSanitizer) sanitizer: DomSanitizer,
        @Inject(ElementRef) elementRef: ElementRef,
        @Inject(bl.UmbracoAjaxService) umbracoAjaxService: bl.UmbracoAjaxService,
        @Inject(bl.ConfigService) configService: bl.ConfigService,
        @Inject(bl.TranslatorService) translatorService: bl.TranslatorService,
        @Inject(ValidationService) validationService: ValidationService,
        @Inject(index.PriceCalculatorBySquareMeters) private priceCalculatorBySquareMeters: index.PriceCalculatorBySquareMeters,
    ) {
        super(zone, formBuilder, sanitizer, elementRef, umbracoAjaxService, configService, translatorService, validationService);
    }

    setActiveVariation(variation: PriceVariation) {
        this.activePriceSet.name = variation.heading;
        this.activePriceSet.unitPrice = variation.unitPrice;
        this.setActive(this.activePriceSet);
    }

    setActive(set: PriceSet.PriceSet): void {
        super.setActive(set);
        this.priceCalculatorBySquareMeters.setPrice(this.activePriceSet);
    }

    ngOnInit(): void {
        super.ngOnInit();
        const self = this;

        // Copy over any settings to self instance.
        $.extend(self, self.options);

        self.calculatorForm = self.formBuilder.group({
            squareMeters: ["", ValidationService.number],
            variation: [0, ValidationService.number]
        });

        const squareMetersControl = self.calculatorForm.get("squareMeters");
        const variationControl = self.calculatorForm.get("variation");
        variationControl.valueChanges.subscribe((index: any) => {
            let variation: PriceVariation = self.priceVariations[index];
            self.setActiveVariation(variation);
            self.activeVariationIndex = index;

            // Trigger an update
            squareMetersControl.patchValue(squareMetersControl.value);
        });

        squareMetersControl.valueChanges.debounceTime(500).throttle(() => { return self.onPriceChangesLock; }).subscribe((value: any) => {
            value = parseInt(value || 0, 10);

            if (value <= 0 || _.isNaN(value)) {
                self.onPriceChangesLock.next(null);
                return;
            }

            self.priceCalculatorBySquareMeters.setSquareMeters(value);
            self.price = self.priceCalculatorBySquareMeters.calculate();

            // Call next to release throttle and signal new price.
            self.onPriceChangesLock.next(self.price);
        });
    }
}



