import { NgZone, Inject, Input, ElementRef, OnInit } from "@angular/core";
import { FormGroup, FormBuilder } from "@angular/forms";
import { DomSanitizer } from "@angular/platform-browser";
import * as Rx from "rxjs/Rx";
import * as $ from "jquery";
import * as bl from "../../../blueleet";
import { ValidationService } from "../../../validation.service";
import * as index from "../index";

export interface IWidgetPriceCalculatorOptions {
    unitPrice: number;
    discountRate: number;
    incVat: boolean;
    vatRate: number;
    materialVat: number;
    travelCost: number;
}

export abstract class WidgetPriceCalculatorComponent<TOptType extends IWidgetPriceCalculatorOptions> implements OnInit {
    calculatorForm: FormGroup;
    defaultPriceSet: index.PriceSet;
    activePriceSet: index.PriceSet;
    price: index.Price;
    fullPriceMessage: string;
    priceDiscountMessage: string;
    showResultMessage: boolean = false;

    /*
    *  Editor settings
    */
    @Input() title: string;
    @Input() question: string;
    @Input() options: TOptType | any;
    @Input("sqm-min") sqmMin: number = 10;

    protected onPriceChangesLock: Rx.BehaviorSubject<index.Price> = new Rx.BehaviorSubject<index.Price>(null);
    public onPriceChanges: Rx.Observable<index.Price> = this.onPriceChangesLock.asObservable();

    constructor(
        @Inject(NgZone) protected zone: NgZone,
        @Inject(FormBuilder) protected formBuilder: FormBuilder,
        @Inject(DomSanitizer) protected sanitizer: DomSanitizer,
        @Inject(ElementRef) protected elementRef: ElementRef,
        @Inject(bl.UmbracoAjaxService) protected umbracoAjaxService: bl.UmbracoAjaxService,
        @Inject(bl.ConfigService) protected configService: bl.ConfigService,
        @Inject(bl.TranslatorService) protected translatorService: bl.TranslatorService,
        @Inject(ValidationService) protected validationService: ValidationService) {
    }

    setActive(set: index.PriceSet) {
        this.activePriceSet = set;
    }

    ngOnInit(): void {
        const self = this;
        if (self.options) {
            self.options = JSON.parse(self.options);
        } else {
            self.options = {};
        }

        $.extend(self.options, { showFullPrice: true, showReducedPrice: true, showVariations: true }, self.options);

        self.defaultPriceSet = new index.PriceSet(self.question,
            self.options.unitPrice,
            self.options.discountRate,
            self.options.incVat,
            self.options.vatRate,
            self.options.materialVat,
            self.options.travelCost);

        self.setActive(self.defaultPriceSet);

        self.onPriceChanges.subscribe(price => {
            if (price === null) {
                self.showResultMessage = false;
                return;
            }

            let p1 = self.translatorService.smartTranslate("PriceCalculator.FullPriceMessage", { Price: price.utanReduction.incMoms });
            let p2 = self.translatorService.smartTranslate("PriceCalculator.DiscountPriceMessage", { Price: price.medReduction.incMoms });

            Promise.all([p1, p2]).then(values => {
                this.zone.run(() => {
                    self.fullPriceMessage = values[0];
                    self.priceDiscountMessage = values[1];
                    self.showResultMessage = true;
                });
            });
        });
    }
}



