import { Component, NgZone, Inject, Input, ElementRef, OnInit } from "@angular/core";
import { FormBuilder } from "@angular/forms";
import { DomSanitizer } from "@angular/platform-browser";
import * as $ from "jquery";
import * as bl from "../../../blueleet";
import { ValidationService } from "../../../validation.service";
import * as index from "../index";
import * as PriceSet from "../entities/priceSet";
import { WidgetPriceCalculatorComponent, IWidgetPriceCalculatorOptions } from "./widget-priceCalculator.component";
const _ = require("lodash");

export class Session {
    public occasion: number;
    public description: string;
}

export class HourBySquareMeter {
    public hour: string;
    public squareMeter: string;
}

export interface IWidgetPriceCalculatorByHoursOptions extends IWidgetPriceCalculatorOptions {
    sessions: Session[];
    hourBySquareMeters: HourBySquareMeter[];
}

@Component({
    selector: "widget-price-calculator-by-hours",
    template: require("./templates/widget-priceCalculatorByHours.html"),
    styles: [require("./widget-priceCalculator.scss").toString()],
    providers: [index.PriceCalculatorByHours]
})
export class WidgetPriceCalculatorByHoursComponent extends WidgetPriceCalculatorComponent<IWidgetPriceCalculatorByHoursOptions> implements OnInit, IWidgetPriceCalculatorByHoursOptions {
    activeSessionIndex: number = 0;

    /*
    *  Editor settings
    */
    @Input() sessionTitle: string;
    @Input() placeholderTitle: string;

    /*
    *  Calculator settings
    */
    @Input() unitPrice: number;
    @Input() discountRate: number;
    @Input() incVat: boolean;
    @Input() vatRate: number;
    @Input() materialVat: number;
    @Input() travelCost: number;
    @Input() sessions: Session[] = [];
    @Input() hourBySquareMeters: HourBySquareMeter[] = [];

    hoursBySqmPairs: any[];
    sqmList: any[];

    constructor(
        @Inject(NgZone) zone: NgZone,
        @Inject(FormBuilder) formBuilder: FormBuilder,
        @Inject(DomSanitizer) sanitizer: DomSanitizer,
        @Inject(ElementRef) elementRef: ElementRef,
        @Inject(bl.UmbracoAjaxService) umbracoAjaxService: bl.UmbracoAjaxService,
        @Inject(bl.ConfigService) configService: bl.ConfigService,
        @Inject(bl.TranslatorService) translatorService: bl.TranslatorService,
        @Inject(ValidationService) validationService: ValidationService,
        @Inject(index.PriceCalculatorByHours) private priceCalculatorByHours: index.PriceCalculatorByHours,
    ) {
        super(zone, formBuilder, sanitizer, elementRef, umbracoAjaxService, configService, translatorService, validationService);
    }

    setActive(set: PriceSet.PriceSet): void {
        super.setActive(set);
        this.priceCalculatorByHours.setPrice(this.activePriceSet);
    }

    ngOnInit(): void {
        super.ngOnInit();
        const self = this;

        // Copy over any settings to self instance.
        $.extend(self, self.options);

        // Partition 'hourBySquareMeters' by two skipping first
        this.hoursBySqmPairs = _.chunk(this.hourBySquareMeters, 2);
        this.sqmList = _.map(this.hoursBySqmPairs, (pair: HourBySquareMeter[], index: number) => {
            let low = pair[0];
            let highe = pair[1];
            let text: string;

            if (highe) {
                text = `${parseInt(low.squareMeter, 10)} - ${parseInt(highe.squareMeter, 10)}`;
            } else {
                text = `${parseInt(low.squareMeter, 10)}`;
            }

            // Bug: ng-select can not handle index with 0 as it is skipped.
            let result = { id: index + 1, text: text };
            return result;
        });

        self.calculatorForm = self.formBuilder.group({
            squareMeters: ["", ValidationService.number],
            session: [0, ValidationService.number]
        });

        const squareMetersControl = self.calculatorForm.get("squareMeters");
        const sessionControl = self.calculatorForm.get("session");
        sessionControl.valueChanges.subscribe((index: any) => {
            self.activeSessionIndex = index;

            // Trigger an update
            squareMetersControl.patchValue(squareMetersControl.value);
        });

        squareMetersControl.valueChanges.debounceTime(500).throttle(() => { return self.onPriceChangesLock; }).subscribe((value: any) => {

            // Bug: ng-select can not handle index with 0 as it is skipped.
            let hour = self.hoursBySqmPairs[value.id - 1][0].hour;
            let session: Session = self.sessions[self.activeSessionIndex];

            self.priceCalculatorByHours.setHours(hour);
            self.priceCalculatorByHours.setTimesYear(session.occasion);
            self.price = self.priceCalculatorByHours.calculate();

            // Call next to release throttle and signal new price.
            self.onPriceChangesLock.next(self.price);
        });
    }

    selected(value: any): void {
        const self = this;
        const squareMetersControl = self.calculatorForm.get("squareMeters");
        squareMetersControl.patchValue(value);
    }
    
    typed(value: any): void {
        console.log('New search input: ', value);
    }

    refreshValue(value: any): void {
        console.log(value);
    }
}



